import React, { useState, useEffect, Suspense } from "react";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import * as SecureStore from "expo-secure-store";
import colors from "./config/colors";
import AppLoading from "expo-app-loading";
import { useFonts, Poppins_800ExtraBold, Poppins_500Medium, Poppins_100Thin, Poppins_400Regular, Poppins_300Light } from "@expo-google-fonts/poppins";

const Stack = createNativeStackNavigator();

// Dynamically import screens to reduce initial load time
const Loginstudent = React.lazy(() => import("./src/screens/Loginstudent"));
const Loginteacher = React.lazy(() => import("./src/screens/Loginteacher"));
const Welcome = React.lazy(() => import("./src/screens/Welcome"));
const Teacherhome = React.lazy(() => import("./src/screens/Teacherhome"));
const Takeattendance = React.lazy(() => import("./src/screens/TakeAttendance"));
const Studenthome = React.lazy(() => import("./src/screens/Studenthome"));
const CreateNotice = React.lazy(() => import("./src/screens/CreateNotice"));
const PreviousNotices = React.lazy(() => import("./src/screens/PreviousNotices"));
const LeaveAppeals = React.lazy(() => import("./src/screens/BrowseLeaveAppeals"));
const AppealLeave = React.lazy(() => import("./src/screens/AppealLeave"));
const AttendanceRecord = React.lazy(() => import("./src/screens/AttendanceRecord"));
const ProgressChart = React.lazy(() => import("./src/screens/progressChart"));
const Rewards = React.lazy(() => import("./src/screens/rewards"));
const Calendar = React.lazy(() => import("./src/screens/Calendar"));
const Attendanceinfo = React.lazy(() => import("./src/screens/Attendanceinfo"));

export default function App() {
  const [date, setDate] = useState(null);
  const [defaultScreen, setDefaultScreen] = useState("Welcome");

  const getToken = async () => {
    const token = await SecureStore.getItemAsync("user");
    if (token) {
      const role = JSON.parse(token).role;
      if (role === "student") {
        setDefaultScreen("Calendar"); // Navigate to Calendar after login
      } else {
        setDefaultScreen("Teacherhome");
      }
    }
  };

  useEffect(() => {
    getToken();
    let today = new Date();
    let date = today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate();
    setDate(date);
  }, []);

  let [fontsLoaded] = useFonts({
    Poppins_800ExtraBold,
    Poppins_500Medium,
    Poppins_100Thin,
    Poppins_400Regular,
    Poppins_300Light,
  });

  if (!fontsLoaded) {
    return <AppLoading />;
  }

  return (
    <NavigationContainer>
      <Suspense fallback={<AppLoading />}>
        <Stack.Navigator initialRouteName={defaultScreen}>
          <Stack.Screen
            name="Attendance"
            component={Takeattendance}
            options={{
              title: "Attendance: " + date,
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="Welcome"
            component={Welcome}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="Loginp"
            component={Loginstudent}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="Logint"
            component={Loginteacher}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="Teacherhome"
            component={Teacherhome}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="progressChart"
            component={ProgressChart}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="rewards"
            component={Rewards}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="Studenthome"
            component={Studenthome}
            options={{
              headerShown: false,
            }}
          />
          <Stack.Screen
            name="Calendar"
            component={Calendar}
            options={{
              title: "Attendance Calendar",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="Attendancerecord"
            component={AttendanceRecord}
            options={{
              title: "Attendance record",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="CreateNotice"
            component={CreateNotice}
            options={{
              title: "Create Notice",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="PreviousNotices"
            component={PreviousNotices}
            options={{
              title: "Previous Notices",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="BrowseLeaveAppeals"
            component={LeaveAppeals}
            options={{
              title: "Leave Appeals",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="AppealLeave"
            component={AppealLeave}
            options={{
              title: "Appeal leave",
              headerStyle: {
                backgroundColor: colors.primary,
              },
              headerTintColor: colors.white,
            }}
          />
          <Stack.Screen
            name="Studentinfo"
            component={Attendanceinfo}
            options={{
              title: "",
              headerTransparent: true,
              headerTintColor: colors.white,
            }}
          />
        </Stack.Navigator>
      </Suspense>
    </NavigationContainer>
  );
}
